.App {
  text-align: center;
}
body,html,h1,p {padding: 0;margin: 0;}
.black_logo {padding:10px 0;color: #fff;background: #000;}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.list {max-width: 500px;margin: 30px auto;padding: 0;text-align: left;list-style: none;}
.list > li {padding: 10px 15px; border-bottom: 1px solid #ddd;}
.list > li:first-child {border-top: 1px solid #ddd;}
.list > li h1 {display: flex;align-items: center; font-size: 16px;}
.list > li p {font-size: 14px;margin-top: 10px;}
.list > li .like {margin-left: 20px;display: flex;justify-content: center;align-items: center;width:100px;height: 30px;border: 1px solid #ddd;font-size: 14px;font-weight: 400;cursor: pointer;}

.modal {max-width: 500px;padding:15px;background: #eee;margin: 0 auto;}